body {
    color: #333333;
    background-color: white;
    margin: 24px;
    font-family: sans-serif;
}

.title {
    text-align: center;
    text-transform: uppercase;
    font-size: 32px;
    font-weight: bold;
    width: 100%;
    color: blue;
}

.layout {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 32px;
    width: 100%;
}

.panel {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 320px;
    gap: 48px;
}

.round {
    font-size: 24px;
}

.board {
    display: flex;
    flex-direction: column;
    gap: 24px;
    width: calc(100vw - 320px);
    align-items: center;
    margin-top: 64px;
    height: 100vh;
}

.board-row {
    display: flex;
    flex-direction: row;
    gap: 24px;
}

.board svg {
    width: 100%;
    height: 100%;
}

.selection-size {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 36px;
    width: calc(100vw - 320px);
    margin-top: 64px;
    gap: 64px;
}

.selection-row {
    display: flex;
    flex-direction: row;
    gap: 36px;
    align-items: flex-start;
}

.size {
    height: 160px;
    width: 160px;
    text-align: center;
    line-height: 160px;
    border: 1px solid #888888;
    cursor: pointer;
}

.card-selector-layout {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    gap: 24px;
}

.card-selector-layout > .not-selected {
    opacity: 0.5;
    cursor: pointer;
}

.card {
    width: 160px;
    height: 200px;
    margin: 0;
    box-sizing: border-box;
    background-color: #eeeeee;
}

.card img {
    width: 160px;
    height: 200px;
}

.card.hidden {
    background-color: #666666;
}


.card.chosen {
    border: 4px solid #666666;
    background-color: #eeeeee;
}

.card:hover.hidden {
    cursor: pointer;
    background-color: #333333;
}

.background {
    position: fixed;
    z-index: 20;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: 0;
    background-color: #888888;
    opacity: 0.8;
}

.dialog {
    position: fixed;
    z-index: 40;
    left: calc(50% - 300px);
    right: calc(50% - 300px);
    top: calc(50% - 150px);
    bottom: calc(50% - 150px);
    border: 8px solid #333333;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    font-size: 48px;
    padding: 24px;
    background-color: white;
    opacity: 1;
}

.dialog p {
    font-size: 40px;
}

button {
    padding: 16px;
    font-size: 20px;
    width: 200px;
    background-color: #1a73e8;
    border: none;
    color: white;
    cursor: pointer;
}
